h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

dl, dd {
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  vertical-align: top;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  border: none;
  width: 100%;
  height: 100%;
}

body {
  padding: 0;
  margin: 0;
  font-size: 14px;
  letter-spacing: -0.2px;
  word-break: keep-all;
  font-family: sans-serif;
  overflow-x: hidden;
}

.blind {
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.mobile_wrap {
  max-width: 414px;
}
.mobile_title {
  font-weight: normal;
  font-size: 18px;
  position: absolute;
  top: -35px;
  left: 0;
  color: #fff;
}

.device_wrap {
  position: relative;
  width: 100%;
  height: calc(100vh - 279px);
  margin: 0 auto;
  border-radius: 8px;
  border-width: 50px 35px;
  border-color: #000;
  border-style: solid;
  z-index: 1000;
  box-sizing: border-box;
}
@media screen and (min-width: 992px) {
  .device_wrap {
    height: calc(100vh - 234px);
  }
}

.contents_wrap {
  position: relative;
  max-width: 344px;
  width: 100%;
  box-sizing: border-box;
}

@media screen and (min-width: 992px) {
  .container {
    display: flex;
  }
}

.main_flex_wrap .device_wrap:not(:last-child) {
  margin-bottom: 50px;
}
@media screen and (min-width: 1280px) {
  .main_flex_wrap {
    display: flex;
  }
  .main_flex_wrap .pc_wrap {
    margin-right: 20px;
  }
  .main_flex_wrap .device_wrap:not(:last-child) {
    margin-bottom: 0;
  }
}
.main_header {
  display: flex;
  padding: 20px 26px;
  box-sizing: border-box;
  border-bottom: solid 1px #ccc;
  background-color: #fff;
  align-items: center;
}
.main_header_logo {
  vertical-align: top;
}
.main_header_logo_link {
  display: inline-block;
}
.main_header_logo_wrap {
  flex: 1;
}
.main_header_link {
  position: relative;
  color: #000;
}
.main_header_link.new::after {
  position: absolute;
  top: 0;
  right: -13px;
  display: block;
  background-image: url(../sprite/sprite.png);
  background-position: -132px -111px;
  width: 10px;
  height: 10px;
  content: "";
}
@media screen and (min-width: 992px) {
  .main_nav {
    min-width: 240px;
    height: 100%;
    min-height: calc(100vh - 140px);
    background-color: #fff;
    padding: 47px 27px;
    box-sizing: border-box;
    border-right: solid 1px #ccc;
  }
}
.main_nav .device_type {
  line-height: 12px;
  font-size: 12px;
  color: #fff;
  font-weight: bold;
  background-color: #6eb327;
  padding: 0 5px;
}
.main_nav_list {
  display: flex;
  white-space: nowrap;
  overflow-x: auto;
}
@media screen and (min-width: 992px) {
  .main_nav_list {
    display: block;
    width: auto;
    overflow-x: initial;
    white-space: initial;
  }
}
.main_nav_item {
  margin: 5px;
}
@media screen and (min-width: 992px) {
  .main_nav_item {
    margin: 10px;
  }
  .main_nav_item:first-child {
    margin: 0;
  }
  .main_nav_item:not(:first-child) {
    margin: 20px 0 0 0;
  }
}
@media screen and (max-width: 991px) {
  .main_nav_text {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
  }
}
@media screen and (min-width: 992px) {
  .main_nav_text {
    font-size: 20px;
  }
}
.main_nav_link {
  position: relative;
  display: inline-block;
  color: #000;
  font-size: 14px;
  padding: 5px 0 3px;
}
.main_nav_sub_list {
  display: flex;
}
@media screen and (min-width: 992px) {
  .main_nav_sub_list {
    display: block;
    margin-top: 5px;
  }
}
.main_nav_sub_item {
  padding-top: 5px;
  padding-bottom: 5px;
}
.main_nav_sub_item:not(:last-child) {
  margin-right: 15px;
}
@media screen and (min-width: 992px) {
  .main_nav_sub_item:not(:last-child) {
    margin-right: 0;
  }
  .main_nav_sub_item.division {
    border-bottom: 1px dashed #ccc;
  }
}
.main_nav_sub_text {
  position: relative;
}
.main_nav_sub_text::after {
  position: absolute;
  left: 0;
  bottom: -2px;
  display: block;
  width: 0%;
  height: 1px;
  background-color: #000;
  transition: width 0.4s;
  content: "";
}
.main_nav_sub_text:hover::after {
  width: 100%;
}
.main_section:not(:last-child) {
  margin-bottom: 30px;
}
.main_title {
  margin-bottom: 10px;
}
.main_info_text {
  display: block;
}
.main_info_text:not(:last-child) {
  margin-bottom: 10px;
}
.main_info_item {
  position: relative;
  padding-left: 10px;
}
.main_info_item:not(:last-child) {
  margin-bottom: 7px;
}
.main_info_item small {
  margin-left: 10px;
  color: #6eb327;
}
.main_info_item::before {
  display: block;
  width: 5px;
  height: 5px;
  position: absolute;
  left: 0;
  top: 5px;
  border-radius: 50%;
  background-color: #6eb327;
  content: "";
}
.main_contents {
  padding: 47px 38px;
}
@media screen and (min-width: 992px) {
  .main_contents {
    flex: 1;
    box-sizing: border-box;
    width: calc(100% - 240px);
  }
}
.main_footer {
  height: 50px;
  padding: 16px;
  background-color: #f1f1f1;
  text-align: center;
  box-sizing: border-box;
}
.main_footer_text {
  font-size: 13px;
  color: #333;
}

.new_tab_link {
  position: relative;
  display: block;
  color: #fff;
  padding: 10px 15px 10px 10px;
}
.new_tab_link::after {
  position: absolute;
  top: 10px;
  right: 0;
  display: block;
  background-image: url(../sprite/sprite.png);
  background-position: -148px -127px;
  width: 10px;
  height: 10px;
  content: "";
}
.new_tab_link_wrap {
  position: absolute;
  bottom: -40px;
  right: 0;
  z-index: 10000;
}